.App {
  background-color: #222;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
/*  align-items: center;
  justify-content: center;*/
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 4px;
}

main {
  margin-top: 70px;
}

.appbar-label {
  position: absolute;
  top: 0px;
  right: 10px;
  height: 60px;
  display: flex;
  align-items: center;
  text-align: center;
}

.drawer {
  background-color: #333 !important;
}

.top-bar {
  width: 100%;
  height: 60px;
  background-color: #444;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.skills {
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 100%;
}

.nav-link {
  color: white;
  font-size: 20px;
  text-decoration: none;
}

.skill {
  display: flex;
  align-items: center;
  width: 100%;
  height: 80px;
  border-radius: 100vh;
  background-color: #444;
  text-align: center;
}

.skill-content {
  display: flex;
  align-items: center;
  margin-left: 12px;
}

.skill-content p {
  margin-left: 8px;
  font-size: 20px;
}

.drawerActivator {
    position: absolute;
    top: 8px;
    left: 8px;
    background: transparent;
    border: none;
    color: white;
    font-size: 30px;
  }

.ButtonRow {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.outlinedButton {
    border: 2px solid white;
    color: white;
    background-color: transparent;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
}

h1, h2, h3 {
  margin: 0;
  padding: 0;
  text-align: center;
}